<template>
  <div style="margin:10px">
    <Button type="primary" style="margin-bottom:10px;" @click='backHome'>返回上一级</Button>
    <div>
      <div class='sec-header'>店铺信息</div>
      <div class="sec-main">
        <div class="sec-main-avatar">
          <img :src="store.picture" v-if="store.picture" style="width: 80px;height: 80px;border-radius: 50%;vertical-align: middle"/>
          <div v-else style="width: 80px;display: inline-block;">
            <Avatar style="background-color: #3bc0c3;" icon="android-calendar" size="large" />
          </div>
          <span class="mem-name">{{store.managername}}</span>
        </div>
        <div class='sec-main-detail'>
          <span>店铺名称:</span><span>{{store.name||'无'}}</span>
        </div>
        <div class='sec-main-detail'>
          <span>管理员电话:</span><span>{{store.telphone||'无'}}</span>
        </div>
        <div class='sec-main-detail'>
          <span>店铺地址:</span><span>{{store.address||'无'}}</span>
        </div>
        <div class='sec-main-detail'>
          <span>店铺总会员:</span><span>{{store.totalnum||'无'}}</span>
        </div>
        <div class='sec-main-detail'>
          <span>奖励收入:</span><span>{{store.income||'无'}}</span>
        </div>
        <div class='sec-main-detail'>
          <span>店铺总业绩:</span><span>{{store.performance||'无'}}</span>
        </div>
        <div class='sec-main-detail'>
          <span>店铺邀请码:</span><span>{{store.incode||'无'}}</span>
        </div>
        <div class='sec-main-detail'>
          <span>店铺推荐人编码:</span><span>{{store.referrer||'无'}}</span>
        </div>
        <div class='sec-main-detail'>
          <span>注册时间:</span><span>{{store.createtime||'无'}}</span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      store: {}
    };
  },
  mounted() {
    let that = this;
    that.$sitemap.prvname = "店铺管理";
    that.$sitemap.currname = "店铺详情";
    that.getstoreDetail();
  },
  methods: {
    backHome() {
      this.$router.go(-1);
    },
    //获取会员详情
    getstoreDetail() {
      let that = this;
      let data = { storeid: this.$route.query.id };
      that.$http
        .post(this.$api.store_get, data)
        .then(res => {
          let resData = res.data.data;
          if (res.data.result == "1" && resData) {
            that.store = resData || {};
          } else {
            that.$Message.error(res.data.msg);
          }
        })
        .catch(err => {
          that.$Message.error("服务器异常");
        });
    }
  }
};
</script>
<style scoped>
.sec-header {
  background-color: #f1f4f5;
  height: 40px;
  line-height: 40px;
  padding: 0 0 0 20px;
  color: #3bc0c3;
}
.sec-main {
  min-height: 100px;
  padding: 20px 30px;
}
.sec-main-detail {
  line-height: 40px;
}
.sec-main-avatar {
  margin-bottom: 10px;
  padding: 5px 0;
}
.sec-main-avatar > span:last-of-type {
  font-size: 16px;
  margin-left: 70px;
  font-weight: bold;
  vertical-align: middle;
}
.sec-main-detail > span:first-of-type {
  width: 150px;
}
.sec-main-detail span {
  display: inline-block;
  padding: 0 5px;
  height: 30px;
}
</style>



